.revisions {
  position: fixed;
  right: 32px;
  bottom: 32px;
  z-index: 9999;
}
.revisions--active {
    width: 500px;
    height: 500px;
}
.revisions__button {
    border-radius: 50%;
    background-color: #ff2626;
    width: 50px;
    height: 50px;
}
.revisions__button:hover {
      cursor: pointer;
}
.revisions__button span {
      position: absolute;
      top: 50%;
      left: 50%;
      background-color: #fff;
      width: 25px;
      height: 5px;
}
.revisions__button span:nth-child(1) {
        top: 25px;
        left: 25%;
        transform: translate(0, -3px) rotate(90deg);
}
.revisions__button span:nth-child(2) {
        transform: translate(-50%, -50%);
}
.revisions__form iframe {
    border: 2px solid #ff2626;
    width: 500px;
    height: 500px;
}
.revisions__close {
    text-align: right;
    color: #ff2626;
}
.revisions__close:hover {
      cursor: pointer;
}
.button {
  position: relative;
  transition: color .45s ease-in-out;
  border: 1px solid #0f0f0f;
  cursor: pointer;
  padding: 11px 20px;
  overflow: hidden;
}
.button:hover {
    color: #8f8f8f;
}
.button:hover span {
      width: 225%;
      height: calc(200px*2.25);
}
.button:active {
    opacity: .7;
}
.button span {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
    transition: height .45s ease-in-out, width .45s ease-in-out;
    z-index: -1;
    border-radius: 50%;
    background-color: #0f0f0f;
    width: 0;
    height: 0;
}
