  border-left: 5px solid #0092ff;
  text-indent: -9999em;
  font-size: 10px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  -webkit-animation: loaderSpinner 1.1s infinite linear;
}
.loader, .loader:after {
    border-radius: 50%;
    width: 40px;
    height: 40px;
}



